import React, {Component} from 'react';
import './Button.css'

class KeyPadComponent extends Component {

    render() {
        return (
            <div className="button-wrap">
                <button className="Sbtn" name="(" onClick={e => this.props.onClick(e.target.name)}>(</button>
                <button  className="Sbtn" name=")" onClick={e => this.props.onClick(e.target.name)}>)</button>
                <button  className="Sbtn" name="CE" onClick={e => this.props.onClick(e.target.name)}>CE</button>
                <button  className="Sbtn" name="C" onClick={e => this.props.onClick(e.target.name)}>C</button><br/>


                <button   className="Nbtn" name="1" onClick={e => this.props.onClick(e.target.name)}>1</button>
                <button className="Nbtn" name="2" onClick={e => this.props.onClick(e.target.name)}>2</button>
                <button className="Nbtn" name="3" onClick={e => this.props.onClick(e.target.name)}>3</button>
                <button className="Sbtn" name="+" onClick={e => this.props.onClick(e.target.name)}>+</button><br/>


                <button className="Nbtn" name="4" onClick={e => this.props.onClick(e.target.name)}>4</button>
                <button className="Nbtn" name="5" onClick={e => this.props.onClick(e.target.name)}>5</button>
                <button className="Nbtn" name="6" onClick={e => this.props.onClick(e.target.name)}>6</button>
                <button className="Sbtn" name="-" onClick={e => this.props.onClick(e.target.name)}>-</button><br/>

                <button className="Nbtn" name="7" onClick={e => this.props.onClick(e.target.name)}>7</button>
                <button className="Nbtn" name="8" onClick={e => this.props.onClick(e.target.name)}>8</button>
                <button className="Nbtn" name="9" onClick={e => this.props.onClick(e.target.name)}>9</button>
                <button className="Sbtn" name="*" onClick={e => this.props.onClick(e.target.name)}>x</button><br/>


                <button className="Sbtn"name="." onClick={e => this.props.onClick(e.target.name)}>.</button>
                <button className="Nbtn" name="0" onClick={e => this.props.onClick(e.target.name)}>0</button>
                <button className="Sbtn"name="=" onClick={e => this.props.onClick(e.target.name)}>=</button>
                <button className="Sbtn" name="/" onClick={e => this.props.onClick(e.target.name)}>รท</button><br/>
            </div>
        );
    }
}


export default KeyPadComponent;